body {
  background-color: #1a1c20;
  color: #f8f1e7;
  font-family: 'Merriweather', serif;
}

.product_img {
  border-radius: 6px;
  border: 2px solid #d4af37;
  max-height: 300px;
  object-fit: cover;
  box-shadow: 0 0 15px rgba(212, 175, 55, 0.2);
}

/* Headings */
h1,
h3,
h5 {
  color: #d4af37;
  font-family: 'Cinzel', serif;
  text-transform: uppercase;
  letter-spacing: 1px;
}

h1 {
  font-size: 2.5rem;
  font-weight: 700;
}

h3 {
  font-size: 1.8rem;
}

h5 {
  font-size: 1.2rem;
  margin-top: 1rem;
}

/* Text paragraphs */
p {
  font-family: 'Georgia', serif;
  font-size: 1rem;
  color: #f8f1e7;
}

/* Form styling */
form {
  background-color: #2b2d31;
  border: 1px solid #444;
  border-left: 4px solid #d4af37;
  border-radius: 8px;
  padding: 2rem;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

input.form-control {
  background-color: #1f1f1f;
  color: #f8f1e7;
  border: 1px solid #7b2d26;
  font-family: 'Courier New', Courier, monospace;
}

input.form-control::placeholder {
  color: #b6b6b6;
  font-style: italic;
}

input.form-control:focus {
  border-color: #d4af37;
  outline: none;
  box-shadow: 0 0 5px rgba(212, 175, 55, 0.5);
}

/* Alerts */
.alert {
  font-family: 'Courier New', Courier, monospace;
  border-radius: 4px;
  margin-bottom: 1rem;
}

.alert-info {
  background: #394867;
  color: #f8f1e7;
  border-left: 4px solid #d4af37;
}

.alert-success {
  background: #2d3e2d;
  color: #d4af37;
  border-left: 4px solid #d4af37;
}

.alert-danger {
  background: #4c1c1a;
  color: #f8f1e7;
  border-left: 4px solid #7b2d26;
}

/* Button */
button.btn-dark {
  background-color: #7b2d26;
  color: #f8f1e7;
  border: 1px solid #d4af37;
  font-weight: bold;
  letter-spacing: 0.5px;
  font-family: 'Cinzel', serif;
}

button.btn-dark:hover {
  background-color: #d4af37;
  color: #1a1c20;
  border-color: #7b2d26;
}

/* Responsive tweaks */
@media (max-width: 768px) {
  .product_img {
    width: 100%;
    max-height: 200px;
  }

  h1 {
    font-size: 1.8rem;
  }
}